import React from 'react';

class Controls extends React.Component {
    changeToMousePointer() {
        this.style.cursor='pointer';
    }
    render() {
        return (
            <div>
            <div>
                    <font size="10">
                        <i class="glyphicon glyphicon-play" id="play" onClick="videoControl('play')"
                            ></i>
                        <i class="glyphicon glyphicon-pause" id="pause" onClick="videoControl('pause')"
                            ></i>
                        <i class="glyphicon glyphicon-plus" id="plus" onClick="videoControl('plus')"
                            ></i>
                        <i class="glyphicon glyphicon-minus" id="minus" onClick="videoControl('minus')"
                            ></i>
                        <i class="glyphicon glyphicon-stop" id="reload" onClick="videoControl('reload')"
                            ></i>
                        <i class="glyphicon glyphicon-headphones" id="mute" onClick="videoControl('mute')"
                            ></i>
                        <i class="glyphicon glyphicon-thumbs-up" id="up" onClick="videoControl('up')"
                             style={{color: 'green'}}><span id="up">0</span></i>
                        <i class="glyphicon glyphicon-thumbs-down" id="down" onClick="videoControl('down')"
                             style={{color: 'red'}}><span id="down">0</span></i>
                        <i class="glyphicon glyphicon-th-list" id="mode" onClick="videoControl('mode')"
                            ></i>
                    </font>
                </div>
                <div>
                    <progress id="progressBar" value="0" max="100" style={{width: '100%'}}></progress>
                </div>
                </div>
        );
    }
}

export default Controls;